'use client'

import {useEffect, useRef} from 'react'
import 'jsoneditor/dist/jsoneditor.min.css'
import JSONEditor from 'jsoneditor'
import Split from 'react-split'
import * as R from 'remeda'
import {EMPTY_OBJECT} from '@nyx/kr-common/constant/Constant'

/**
 * https://github.com/josdejong/jsoneditor
 * https://github.com/josdejong/jsoneditor/tree/master/examples
 * https://github.com/webpatch/JSON-Editor-Extension
 */

const LOCAL_STORAGE_KEY = 'JSON'

export default function JsonEditor() {
    const codeEditorRef = useRef<HTMLDivElement>(null)
    const viewEditorRef = useRef<HTMLDivElement>(null)
    const codeEditorInstance = useRef<JSONEditor | null>(null)
    const viewEditorInstance = useRef<JSONEditor | null>(null)

    useEffect(() => {
        // left code editor
        codeEditorInstance.current = new JSONEditor(codeEditorRef.current!, {
            mode: 'code',
            onChange: () => {
                if (viewEditorInstance.current) {
                    viewEditorInstance.current.set(codeEditorInstance.current!.get())
                    localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(codeEditorInstance.current!.get()))
                }
            }
        })
        // right view editor
        viewEditorInstance.current = new JSONEditor(viewEditorRef.current!, {
            mode: 'tree'
        })

        //  
        if (R.isNullish(localStorage.getItem(LOCAL_STORAGE_KEY))) {
            // localStorage  -> {}  
            codeEditorInstance.current!.set(EMPTY_OBJECT)
        } else {
            // localStorage  ->  
            codeEditorInstance.current!.set(JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY)!))
        }
        viewEditorInstance.current.set(codeEditorInstance.current!.get())

        // destructor
        return () => {
            if (codeEditorInstance.current) {
                codeEditorInstance.current.destroy()
            }
            if (viewEditorInstance.current) {
                viewEditorInstance.current.destroy()
            }
        }
    }, [])

    return (
        <>
            <Split direction="horizontal" cursor="col-resize" style={{display: 'flex', height: '100vh', width: '100vw', padding: '10px'}}>
                <div ref={codeEditorRef} style={{width: '50%', height: '100%'}}></div>
                <div ref={viewEditorRef} style={{width: '50%', height: '100%'}}></div>
            </Split>
        </>
    )
}